.Home {
    display: flex;
    flex-direction: column;
    gap:25px;
}

.Banner {
    height: 171px;
    width: 100%;
    overflow: hidden;
    position: relative;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 25px;
        filter: brightness(70%);
    }
}

.BannerOverlay {
    position: absolute;
    bottom: 50px;
    width: 100%;
    z-index: 1;
}

.BannerQuote {
    margin: 0;
    padding: 1rem;
    color: #fff;
    font-size: 2em;
    font-style: normal;
    font-weight: 500;
    text-align: center;
}

.cardContainer {
    margin-top: 0;
    background-color: #F6F6F6;
}